import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import * as moment from 'moment';

import { CookieService } from 'ngx-cookie-service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { FormBuilder } from '@angular/forms';
import * as _ from 'lodash';  

import { FacilityServiceService } from '../../service/facility-service.service';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { FacilityService } from 'src/app/api/controllers/Facility';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-facility-create',
  templateUrl: './facility-create.component.html',
  styleUrls: ['./facility-create.component.scss']
})
export class FacilityCreateComponent implements OnInit {
  selectedTowersIds:any;
  facility:any = {
    apartmentFacilitySlot:[]
  };
  facilityType:any;
  towerslist:any;
  allowedBlock = [];
  alertMessage:string = "";
  isCategoryError:boolean = false;
  pesoValue:string;


  constructor(private facilityservice : FacilityServiceService, 
    private facilityapiservice:FacilityService,
    private cd: ChangeDetectorRef,
    private cookieService:CookieService,
    private apartmentService:ApartmentService,
    private lookupService:LookupService,
    private routerActivate: ActivatedRoute,
    private router: Router,
    private sharedService:SharedService,private fb: FormBuilder) { }

  loadFacilityLocation() {
    let towerParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }
    this.apartmentService.getApartmentBlockByApartmentId(towerParams).subscribe((res:any) => {
      this.towerslist = res;
  	});
  }

  accessed(event) {
    let accessBlock= [];
    event.forEach(element => {
      accessBlock.push({
        id:0,
        apartmentId:element.apartmentId,
        apartmentBlockId:element.apartmentBlockId,
        apartmentFacilityId:this.facilityType.type == 'create' ? 0 : this.facility.apartmentFacilityId,
        isActive: true,
        insertedBy: parseInt(this.cookieService.get('userId')),
        insertedOn: moment().format(),
        updatedBy: null,
        updatedOn: null
      });
    });
    this.allowedBlock = accessBlock;
  }

  createFacility() {
    this.facility.apartmentFacilityId =  this.facilityType.type == 'create' ? 0 : this.facility.apartmentFacilityId;
    this.facility.apartmentId = parseInt(this.cookieService.get('apartmentId'));
    this.facility.totalallowedperson = this.facility.totalallowedperson;
    this.facility.description = this.facility.description;
    this.facility.minTimeLimit =  moment(this.facility.minTimeLimit).format('HH:mm:ss');
    this.facility.maxTimeLimit = moment(this.facility.maxTimeLimit).format('HH:mm:ss');
    this.facility.ApartmentFacilityAllowedBlock = this.allowedBlock;
    this.facility.apartmentFacilitySlot.forEach((data)=>{
      data.slotBeginTime = moment(data.slotBeginTime).format('HH:mm:ss');
      data.slotEndTime = moment(data.slotEndTime).format('HH:mm:ss');
    })
    let entity = {
      "approvalRequired": true,
      "fileAttachmentId": null,
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": moment().format(),
      "updatedBy": null,
      "updatedOn": null,
    }
    let apartmentFacilityParams = {
      apartmentFacility: {...this.facility,...entity}
    }
    let apiName = this.facilityType.type == 'create' ? 'addApartmentFacility' : 'updateApartmentFacility';
    this.facilityapiservice[apiName](apartmentFacilityParams).subscribe((res:any) => {
      if(res.message){
        let message =  this.facilityType.type == 'create' ? 'Facility added successfully' : 'Facility Updated successfully';
        this.sharedService.setAlertMessage(message);
      }
      else {
        this.isCategoryError = true;
        this.alertMessage = res.errorMessage;
      }
    },
    error => {
      this.isCategoryError = true;
      this.alertMessage = "Server Error";
    }); 
  }

  addSlot() {
    let entity = {
      slotBeginTime: '',
      slotEndTime: '',
      cost: '',
      comment: '',
      apartmentFacilitySlotId: 0,
      apartmentFacilityId: this.facilityType.type == 'create' ? 0 : this.facility.apartmentFacilityId,
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      isActive: true,
		  insertedBy: parseInt(this.cookieService.get('userId')),
		  insertedOn: new Date().toISOString(),
		  updatedBy: 0,
		  updatedOn: null
    }
    this.facility.apartmentFacilitySlot.push(entity);
  }

  deleteSlot(index,data) {
    this.facility.apartmentFacilitySlot.splice(index,1);
  }

  ngOnInit(): void {
    this.loadFacilityLocation();
    let params = {
      LookupTypeId: 101,
      ApartmentId: parseInt(this.cookieService.get('apartmentId')),
    }
    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {
      this.pesoValue = res[0].lookupValueName;
  	});
    this.routerActivate.queryParams.subscribe((params)=>{
      this.facilityType = params;
      if(params.type) {
        if(params.id) {
          let entity = {
            apartmentFacilityId : parseInt(params.id)
          }
          this.facilityapiservice.getApartmentFacilityByFacilityId(entity).subscribe((res:any) => {
            this.facility = res[0];
            this.facility.minTimeLimit = moment(this.facility.minTimeLimit, 'HH:mm:ss').format();
            this.facility.maxTimeLimit = moment(this.facility.maxTimeLimit, 'HH:mm:ss').format();
            this.facility.apartmentBlockId = this.facility.apartmentBlockID;
            delete this.facility.apartmentBlockID;
            this.facility.apartmentFacilitySlot.forEach((ele)=>{
              ele.slotBeginTime = moment(ele.slotBeginTime, 'HH:mm:ss').format();
              ele.slotEndTime = moment(ele.slotEndTime, 'HH:mm:ss').format();
            })
            this.selectedTowersIds = this.facility.apartmentFacilityAllowedBlock
            if(params.type == 'slot') {
              this.addSlot();
            }
          })
        }
      } else {
        this.router.navigate(['ams/facility/list'])
      }
    })
  }

}
